

import axios from 'axios'

export const login=async (email,password)=>{
 try{
  
  const {data}= await axios.post('/api/users/login', {email,password},{'Content-Type':'aplication/json'}
   )//post methodunu yeni bir document olu≈ütururken kullanƒ±yorduk ve bunu request.body aracƒ±lƒ±ƒüƒ±yla yapƒ±yorduk i≈üte burdaki ikinci kƒ±sƒ±m ({email,password}) request body kƒ±smƒ±na yerle≈ütirilen objecttir bu email ve password ise kullanƒ±cƒ±nƒ±n LoginScreen ekranƒ±nda girdiƒüi password ve emaildir.
  
  localStorage.setItem('userInfo', JSON.stringify(data))
  return data
 }  
 catch(error){

    console.error(error)
    return error.response.status
  

}
}
//

export const logout=()=>{
  localStorage.removeItem('userInfo')
  
  

}